body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background: linear-gradient(to right, #fffcfc, #ffffff); /* Gradient background */
  height: 100vh; /* Full height */
  display: flex;
  justify-content: center;
  align-items: top;
}

.container {
  text-align: center;
}

.app {
  /* Style your app container as needed */
  background-color: #f0f0f0;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}